import * as React from "react";
import GlobalStore from "store/GlobalStore";
import UCStore from "store/UCStore";
import Header from "components/header";
import Footer from "components/footer";
import UCView from "views/uc";
import { IS_NODE } from "../../../env";
export default class UCEntry extends React.Component {
    constructor(props) {
        super(props);
        if (!IS_NODE) {
            const { location, match } = props;
            GlobalStore.getInstance({ location, match, cookies: "" });
        }
    }
    render() {
        return (React.createElement("div", null,
            React.createElement(Header, null),
            React.createElement("main", { className: "app-content" },
                React.createElement(UCView, null)),
            React.createElement(Footer, null)));
    }
}
// SSR 在入口组件中获知Store类并初始化用于实例注入
UCEntry.STORE_CLASSES = [GlobalStore, UCStore];



